* {
  font-family:  'Playfair Display', 'sans-serif';
  user-select: none;
  pointer-events: 'none'
}

.font{
  font-family: 'Protest Revolution', 'sans-serif';
}

.page{
  display: flex;
  flex-direction: row;
}

.fixe {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000; /* Adjust as needed */
}

.canvas{
  position: fixed;
  width: 100%;
  z-index: 1;
  height: 100vh;
}

.foreground {
  display: flex;
  position: absolute;
  align-items: center;
  top: 0;
  left: 0;
  align-items: stretch;
  width: 100%;
}

.circle{
  user-select: none;
  z-index: +1;
  position: fixed;
  border: 1px solid black;
  border-radius: 30px;
  padding: 20px;
  top: 0;
  left: 0;
}