
.nav {
    display: flex;
    list-style: none;
    flex-direction: row;
  }
  
  .logo{
    height: 4rem;
    
  }
  /* App.css */
  
  .navlist {
    width: 70px;
    font-size: x-large;
    margin-top: 8rem;
    padding: 0px 0px 10px 0px;
  }
  
  .navlist:before, .navlist:after{
    content: '';
    position: absolute;
    width: 0%;
    height: 2px;
    top: 50%;
    margin-top: 5px;
    background: #000000;
  }
  
  .navlist:before {
    left: -2.5px;
  }
  .navlist:after {
    right: 2.5px;
    background: #000000;
    transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
  }
  
  .navlist:hover:before {
    background: #000000;
    width: 100%;
    font-size: xx-large;
    transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
  }
  
  .navlist:hover:after {
    font-size: x-large;
    background: transparent;
    width: 100%;
    transition: 0s;
  }